IonItem {
  --background: 'transparent';
}
.login-item {
  --background: 'transparent';
  margin-top: 10px;
}
.ion-activatable {
  position: relative;
  overflow: hidden;
}
.app-title {
  background: linear-gradient(to right, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3);
  -webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
  font-size: 40px;
}

.welcome-msg {
  background: linear-gradient(to right, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3);
  -webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
  font-size: 40px;
}

.slide1 {
  background-image: linear-gradient(#000000, #0000A0, transparent, transparent, transparent);
}
